'use client';

import { useState } from 'react';
import { motion } from 'framer-motion';

interface AnimatedSvgProps {
  svgPath: string;
  className?: string;
}

export default function AnimatedSvg({ svgPath, className = '' }: AnimatedSvgProps) {
  const [isHovered, setIsHovered] = useState(false);

  return (
    <motion.svg
      viewBox="0.00 0.00 1024.00 572.00"
      xmlns="http://www.w3.org/2000/svg"
      version="1.1"
      className={`w-full h-auto ${className}`}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <motion.g
        stroke="#808080"
        strokeWidth="2.00"
        fill="none"
        strokeLinecap="butt"
        vectorEffect="non-scaling-stroke"
      >
        <motion.path
          d={svgPath}
          strokeDasharray={1000}
          initial={{ strokeDashoffset: 1000 }}
          animate={{ strokeDashoffset: isHovered ? 0 : 1000 }}
          transition={{
            duration: 3,
            ease: 'easeInOut',
          }}
        />
      </motion.g>
    </motion.svg>
  );
}
